.page-header {
  display: flex;
  flex-direction: column;

  flex-wrap: wrap;
  margin: 0 auto;
}

.page-header__menu-close {
  width: 23px;
  height: 22px;

  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 20px;
  top: 17px;
}

.page-header__button-cross {
  fill: white;
}

.page-header__top {
  width: 100%;
  height: 56px;

  background: white;
  position: relative;
}

.page-header__logo {
  width: 102px;
  height: 83px;

  background: url("../img/logo-sedona-mobile.svg");
  left: 34%;
  position: absolute;
  top: 0;
  z-index: 100;
}

.page-header__intro {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 320px;
  height: 388px;

  background: sandybrown url("../img/bg-back-mobile@1x.jpg") no-repeat left -224px top -1px;
  padding-top: 5px;
  position: relative;

  &::after {
    width: 100%;
    height: 28px;

    background-image: url("../img/bg-intro-triangle-mobile.svg");
    background-repeat: no-repeat;
    content: "";
    position: absolute;
    top: 362px;
  }

  &--photo,
  &--form {
    display: flex;
    justify-content: center;
    align-items: flex-start;

    width: 100%;
    height: 157px;

    background: sandybrown url("../img/bg-back-mobile@1x.jpg") no-repeat left -224px top -232px;
    padding-top: 1px;

    &::after {
      top: 131px;
    }
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header__intro {
    background: sandybrown url("../img/bg-back-mobile@2x.jpg") no-repeat left -224px top 0;
    background-size: 768px auto;

    &--photo,
    &--form {
      background-position: left -225px top -232px;
    }
  }
}

.page-header__title {
  width: 281px;
  height: 214px;

  &--photo,
  &--form {
    width: 229px;
    height: 39px;

    margin-top: 50px;
  }
}

.page-header__menu-open {
  width: 23px;
  height: 23px;

  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  cursor: pointer;
  padding: 0;
  position: absolute;
  right: 20px;
  top: 15px;
}

.page-header__menu-open--disable {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-header__menu-open {
    display: none;
  }

  .page-header__menu-close {
    display: none;
  }

  .page-header__top {
    height: auto;
  }

  .page-header__logo {
    left: 43.5%;
    top: -55px;
  }

  .page-header__intro {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;

    background: sandybrown url("../img/bg-back-tablet@1x.jpg") no-repeat;
    min-height: 518px;

    &::after {
      height: 57px;

      background-image: url("../img/bg-intro-triangle-tablet.svg");
    }

    &--index {
      padding-bottom: 18px;

      &::after {
        top: 462px;
      }
    }

    &--photo,
    &--form {
      background: sandybrown url("../img/bg-back-tablet@1x.jpg") no-repeat left 0 top -227px;
      min-height: 290px;

      &::after {
        top: 234px;
      }
    }
  }


  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header__intro {
      background: sandybrown url("../img/bg-back-tablet@2x.jpg") no-repeat;
      background-size: 768px auto;

      &--photo,
      &--form {
        background-position: left 0 top -227px;
      }
    }
  }

  .page-header__title {
    width: 460px;

    &--index {
      height: 350px;
    }

    &--photo {
      margin-top: -25px;
      height: 79px;
    }

    &--form {
      margin-top: -25px;
      height: 79px;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-header__logo {
    width: 188px;
    height: 96px;

    background: url("../img/logo-sedona-desktop.svg");
    left: 42.3%;
    position: absolute;
    top: -56px;
  }

  .page-header__intro {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;


    background: sandybrown url("../img/bg-back-desktop@1x.jpg") no-repeat;
    min-height: 518px;

    &::after {
      height: 57px;

      background-image: url("../img/bg-intro-triangle-desktop.svg");
    }

    &--index {
      padding-bottom: 15px;

      &::after {
        top: 462px;
        left: 0;
      }
    }

    &--photo,
    &--form {
      background: sandybrown url("../img/bg-back-desktop@1x.jpg") no-repeat left 0 top -217px;
      min-height: 300px;
      padding-top: 7px;

      &::after {
        top: 243px;
        left: 0;
      }
    }
  }


  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header__intro {
      background: sandybrown url("../img/bg-back-desktop@2x.jpg") no-repeat;
      background-size: 1200px auto;

      &--photo,
      &--form {
        background-position: left 0 top -217px;
      }
    }
  }
}
